import {
    Component,
    h,
    Host,
    Prop,
    Element,
    Event,
    EventEmitter,
} from '@stencil/core';
import { classlist } from '../../utils/classlist';

const containerVariantsConfig = {
    variants: {
        showButtons: {
            true: 'border-med border border-solid',
            false: '',
        },
    },
};

const inputVariantsConfig = {
    variants: {
        disabled: {
            false: 'bg-white',
            true: 'bg-surface-1 !text-disabled pointer-events-none border-none',
        },
        readonly: {
            false: 'bg-white',
            true: 'bg-surface-1 text-dark border-none',
        },
        invalid: {
            false: 'border-med focus:border-active-dark focus:ring-active-foreground/30 focus:ring-2',
            true: 'border-error-base focus:ring-destructive-foreground/30 focus:ring-2',
        },
        activePlaceholder: {
            false: 'placeholder:text-disabled',
            true: 'placeholder:text-dark',
        },
        showButtons: {
            true: '',
            false: 'border border-solid',
        },
    },
    compoundVariants: [
        {
            readonly: true,
            disabled: true,
            class: 'bg-surface-1 text-dark',
        },
        {
            readonly: false,
            disabled: false,
            invalid: false,
            class: 'border-med text-dark focus:border-active-foreground focus:ring-active-foreground/30 bg-white focus:ring-2',
        },
    ],
    defaultVariants: {
        disabled: false,
        readonly: false,
        invalid: false,
        activePlaceholder: false,
        showButtons: false,
    },
};

/**
 * @category Form Controls
 * @description A numeric input component for entering numerical values.
 */

@Component({
    tag: 'atui-input-numeric',
    styleUrl: 'atui-input-numeric.scss',
    shadow: false,
})
export class AtuiInputNumericComponent {
    /**
     * Label above the input container
     */
    @Prop()
    label: string;

    /**
     * Short description or validation hint if required
     */
    @Prop()
    hint_text: string;

    /**
     * Optional info icon with detailed tooltip description
     */
    @Prop()
    info_text: string;

    /**
     * Error text displayed when invalid is set
     */
    @Prop()
    error_text: string;

    /**
     * Placeholder text to be shown when no input is passed
     */
    @Prop()
    placeholder: string = '';

    /**
     * Adds a required * to the element
     */
    @Prop()
    required: boolean;

    /**
     * Shows the error text
     */
    @Prop()
    invalid: boolean;

    /**
     * Makes the input read only
     */
    @Prop()
    readonly: boolean;

    /**
     * Disables interaction with the input
     */
    @Prop()
    disabled: boolean = false;

    /**
     * Min selectable value of the input.
     */
    @Prop()
    min = 0;

    /**
     * Max selectable value of the input.
     */
    @Prop()
    max = 100;

    /**
     * Interval between selections.
     */
    @Prop()
    step?: number = 1;

    /**
     * Show buttons inside input or outside
     */
    @Prop()
    show_buttons: boolean = true;

    /**
     * The value of the input
     */
    @Prop({ mutable: true })
    value: number = 0;

    inputEl: HTMLInputElement;
    @Element() el: HTMLAtuiInputElement;

    /**
     * Emits an event containing the text string or number when the input's content is changed
     */
    @Event()
    atuiChange: EventEmitter<string | number>;

    private inputId = `input-${Math.random().toString(36).substring(2, 11)}`;

    handleChange(event: Event): void {
        event.stopPropagation();
        this.value = Number((event.target as HTMLInputElement).value);
        this.atuiChange.emit(this.value);
    }

    handleDecrease = () => {
        if (this.value > this.min) {
            this.value = Math.max(this.value - this.step, this.min);
            this.atuiChange.emit(this.value);
        }
    };

    handleIncrease = () => {
        if (this.value < this.max) {
            this.value = Math.min(this.value + this.step, this.max);
            this.atuiChange.emit(this.value);
        }
    };

    render() {
        const getContainerClassname = classlist(
            'height-[36px] relative flex rounded-md',
            containerVariantsConfig,
        );

        const containerClassname = getContainerClassname({
            showButtons: this.show_buttons,
        });

        const getClassname = classlist(
            'height-[36px] transition[background-color,color,box-shadow] w-full rounded-md p-8 text-right outline-0 duration-300 ease-in-out',
            inputVariantsConfig,
        );

        const classname = getClassname({
            invalid: this.invalid,
            disabled: this.disabled,
            readonly: this.readonly,
            showButtons: this.show_buttons,
        });

        return (
            <Host class="w-full">
                <div class="flex flex-col">
                    <slot name="label" />
                    {(this.label || this.required || this.info_text) && (
                        <atui-form-label
                            label={this.label}
                            for={this.inputId}
                            required={this.required && !this.readonly}
                            info_text={this.info_text}
                        />
                    )}

                    {this.hint_text && (
                        <span
                            class="text-light mb-8 inline-block text-xs leading-tight"
                            data-name="input-hint"
                        >
                            {this.hint_text}
                        </span>
                    )}
                </div>

                <div class={containerClassname}>
                    {this.show_buttons && !this.disabled && !this.readonly && (
                        <atui-button
                            class="border-med rounded-l-md rounded-r-none border-r border-solid"
                            type="secondaryText"
                            icon="remove"
                            onClick={this.handleDecrease}
                            disabled={this.value <= this.min}
                            aria-label="Subtract"
                            data-name="input-subtract"
                        ></atui-button>
                    )}
                    <input
                        id={this.inputId}
                        readonly={this.readonly ? true : undefined}
                        disabled={this.disabled ? true : undefined}
                        required={this.required}
                        type="number"
                        placeholder={`${this.placeholder}`}
                        onInput={(event) =>
                            (this.value = Number(
                                (event.target as HTMLInputElement).value,
                            ))
                        }
                        onChange={(event) => this.handleChange(event)}
                        value={this.value}
                        ref={(el) => (this.inputEl = el)}
                        class={classname}
                        data-name="input"
                    />
                    {this.show_buttons && !this.disabled && !this.readonly && (
                        <atui-button
                            class="border-med rounded-l-none rounded-r-md border-l border-solid"
                            type="secondaryText"
                            icon="add"
                            onClick={this.handleIncrease}
                            disabled={this.value >= this.max}
                            aria-label="Add"
                            data-name="input-add"
                        ></atui-button>
                    )}
                </div>

                {this.error_text && this.invalid && (
                    <span
                        class="text-error text-sm"
                        data-name="input-error"
                    >
                        {this.error_text}
                    </span>
                )}
            </Host>
        );
    }
}
